___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primaryColor: #3377ff;
  --whiteColor: #fff;
  --blackColor: #333;
  --greyColor: #f6f8fa;
  --labelColor: #003d99;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  font-family: 'Poppins';
}

body {
  width: 100%;
  height: auto;
}

h1 {
  line-height: 1.5;
}

.navbar {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  padding: 1rem 2.5rem;
  z-index: 10;
  color: var(--whiteColor);
  background: var(--primaryColor);
}

.navbar-logo > a {
  text-decoration: none;
  color: var(--whiteColor);
}
.logo {
  width: 45px;
}

.navbar-toggle-button {
  display: flex;
  align-items: center;
}

.navbar-toggle-button button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 16px;
  cursor: pointer;
}

.navbar-menu {
  width: 400px;
  display: flex;
  justify-content: space-between;
}

.navbar-menu a {
  color: var(--whiteColor);
  text-decoration: none;
}

nav a:hover {
  border-bottom: 2px solid;
  transition: all 0.5s ease;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
}

.menu-toggle {
  display: block;
}

.menu-toggle .bar {
  width: 15px;
  height: 2px;
  margin: 3px auto;
  transition: all 0.3 ease-in-out;
  background-color: var(--whiteColor);
  cursor: pointer;
  -webkit-transition: all 0.3 ease-in-out;
  -moz-transition: all 0.3 ease-in-out;
  -ms-transition: all 0.3 ease-in-out;
  -o-transition: all 0.3 ease-in-out;
}

header {
  min-height: 500px;
  position: relative;
  background-color: #3377ff;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header-title {
  position: absolute;
  top: 35%;
  left: 10%;
  font-weight: bolder;
  font-size: 35px;
  color: var(--whiteColor);
  text-shadow: 3px 2px 0px rgba(0, 0, 0, 0.2);
}

.header-title > span {
  color: var(--labelColor);
}

.header-subtitle {
  position: absolute;
  top: 65%;
  left: 13%;
  font-weight: bolder;
  font-size: 18px;
  color: var(--whiteColor);
  text-shadow: 3px 2px 0px rgba(0, 0, 0, 0.2);
}

.search-box {
  position: absolute;
  display: flex;
  align-items: center;
  min-width: 400px;
  margin-top: 10px;
  top: 70%;
  left: 13%;
}

.search-box input[type='text'] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 4px;
}

.search-box button {
  padding: 8px 12px;
  background-color: var(--labelColor);
  border: none;
  color: var(--greyColor);
  border-radius: 4px;
  cursor: pointer;
}

.search-box button:hover {
  border: 2px solid var(--labelColor);
  color: var(--labelColor);
  background-color: var(--primaryColor);
}

.image-header {
  position: absolute;
  width: 450px;
  top: 10%;
  right: 4%;
  background-image: none;
  /* transform: scaleX(-1); */
}
